// ============================================================================
// 踏月寻仙 - 简化的类型定义 (Simplified Type Definition for UI)
// 用于前端界面读取 MVU 变量数据
// ============================================================================

// 物品类型
export interface Item {
    名称: string;
    描述: string;
    品阶: string;
    数量: number;
}

// 区域类型
export interface Region {
    layer: '天层' | '地层' | '下层';
    danger: number;
    desc: string;
    connections: string[];
}

// 本尊类型
export interface Protagonist {
    // 修炼数据
    等级: number;
    修为: number;
    灵根: string;
    体质: string;
    功法: string;
    本命兵器: string;
    神通列表?: Record<string, string>;
    灵石: number;
    已活岁月: number;
    尝试突破: boolean;
    // 衍生数据
    突破阈值: number;
    寿元上限: number;
    境界描述: string;
    寿元状态: string;
    状态: string;
    进度: string;
    // 行踪
    行踪: {
        当前区域: string;
        所属层级?: string;
        环境描述?: string;
        危险度?: number;
        可用通道?: string[];
        导航信息?: string;
    };
    // 身份
    身份: {
        姓名: string;
        宗门: string;
        出身: string;
    };
    // 物品
    背包: Record<string, Item>;
    法宝: Record<string, Item>;
    杂物袋: Record<string, Item>;
}

// 红颜类型
export interface Companion {
    // 修炼数据
    等级: number;
    修为: number;
    灵根: string;
    体质: string;
    功法: string;
    本命兵器: string;
    神通列表?: Record<string, string>;
    灵石: number;
    已活岁月: number;
    尝试突破: boolean;
    // 衍生数据
    突破阈值: number;
    寿元上限: number;
    境界描述: string;
    寿元状态: string;
    状态: string;
    进度: string;
    // 情感数据
    好感度: number;
    关系: string;
    重要性: string;
}

// NPC 类型
export interface NPC {
    // 修炼数据
    等级: number;
    修为: number;
    灵根: string;
    体质: string;
    功法: string;
    本命兵器: string;
    神通列表?: Record<string, string>;
    灵石: number;
    已活岁月: number;
    尝试突破: boolean;
    // 衍生数据
    突破阈值: number;
    寿元上限: number;
    境界描述: string;
    寿元状态: string;
    状态: string;
    进度: string;
    // NPC 数据
    所在宗门: string;
}

// 完整的变量结构
export interface Schema {
    世界时钟: {
        纪元: string;
        年份: number;
    };
    世界地图: Record<string, Region>;
    世界图志: Record<
        string,
        {
            状态: string;
            事件: string;
        }
    >;
    本尊: Protagonist;
    红颜: Record<string, Companion>;
    NPC图鉴: Record<string, NPC>;
}

// 配置常量
export const CONFIG = {
    REALMS: {
        MAJOR: ['练气', '筑基', '金丹', '元婴', '化神', '炼虚', '合体', '大乘', '渡劫'],
        MINOR: ['初期', '中期', '后期', '大圆满'],
    },
    ELEMENTS: {
        火: { effect: '灼烧', visual: '烈焰', nature: '爆裂', color: '#ff4444' },
        水: { effect: '缠绕', visual: '激流', nature: '柔韧', color: '#4488ff' },
        冰: { effect: '冻结', visual: '寒霜', nature: '极寒', color: '#88ddff' },
        雷: { effect: '麻痹', visual: '紫电', nature: '狂暴', color: '#aa44ff' },
        风: { effect: '切割', visual: '罡风', nature: '无形', color: '#88ff88' },
        土: { effect: '镇压', visual: '岩铠', nature: '厚重', color: '#aa8844' },
        木: { effect: '寄生', visual: '藤蔓', nature: '生生不息', color: '#44aa44' },
        金: { effect: '穿透', visual: '锐金', nature: '锋利', color: '#ffdd44' },
        暗: { effect: '腐蚀', visual: '黑雾', nature: '诡谲', color: '#442244' },
        光: { effect: '净化', visual: '圣辉', nature: '神圣', color: '#ffffaa' },
        混沌: { effect: '湮灭', visual: '灰光', nature: '虚无', color: '#888888' },
    } as Record<string, { effect: string; visual: string; nature: string; color: string }>,
};

// 获取灵根元素颜色
export function getRootColor(root: string): string {
    for (const [elem, data] of Object.entries(CONFIG.ELEMENTS)) {
        if (root.includes(elem)) {
            return data.color;
        }
    }
    return '#aaaaaa'; // 默认灰色
}

// 获取境界等级颜色
export function getRealmColor(level: number): string {
    const majorIdx = Math.floor((level - 1) / 4);
    const colors = [
        '#888888', // 练气 - 灰色
        '#44aa44', // 筑基 - 绿色
        '#4488ff', // 金丹 - 蓝色
        '#aa44ff', // 元婴 - 紫色
        '#ff4444', // 化神 - 红色
        '#ffaa00', // 炼虚 - 橙色
        '#ffdd44', // 合体 - 金色
        '#ffffff', // 大乘 - 白色
        '#ff88ff', // 渡劫 - 粉色
    ];
    return colors[majorIdx] || '#888888';
}

// 获取危险度颜色
export function getDangerColor(danger: number): string {
    if (danger >= 90) return '#ff0000';
    if (danger >= 70) return '#ff4400';
    if (danger >= 50) return '#ff8800';
    if (danger >= 30) return '#ffcc00';
    return '#44aa44';
}
